import { Component, ElementRef, OnInit, ViewChild, Renderer2 } from '@angular/core';
import { from } from 'rxjs';
import { Money } from '../models/Money.model';
import { RequestService } from './../request.service';
import { TabMoneys } from '../models/tabMoneys';
import { Mercancias } from '../models/Mercancias';
import { Router } from '@angular/router';


@Component({
  selector: 'app-form3',
  templateUrl: './form3.component.html',
  styleUrls: ['./form3.component.css']
})
export class Form3Component implements OnInit {

  constructor(private renderer: Renderer2, private request: RequestService, private route: Router) {
    this.tributos = 'n';
    this.cantMil = 'n';
    this.animals = 'n';
    this.quimicProducts = 'n';
    this.mercancias = 'n';
    this.tabMoneys = [];
    this.listMercancias = [];
   }

  moneys: Money[] = [];
  tributos: string;
  cantMil: string;
  animals: string;
  quimicProducts: string;
  mercancias: string;
  tabMoneys: TabMoneys[];
  tabMoney: TabMoneys = null;
  titulo = '';
  valor: number;
  origen = '';
  destino = '';
  money: string = null;
  moneySelected = '';
  listMercancias: Mercancias[];
  mercancia: Mercancias = null;
  cantidad: number = null;
   descripcion = '';
   valorDolar: number = null;

  @ViewChild('containerTab') containerTab: ElementRef;
  @ViewChild('table2') table2: ElementRef;

  ngOnInit(): void {
    this.request.getNameMoney().subscribe((moneys: Money[]) => {
      this.moneys = moneys;
      this.moneys.sort(); });
  }

  enviar(): void{
    if (this.tributos !== 'n' && this.cantMil !== 'n' && this.animals !== 'n' && this.quimicProducts !== 'n'){
      alert('Sus respuestas se han guardado');
      this.route.navigate(['email']);
    }else{
      alert('!Debe llenar todos los campos');
    }
  }

  changeRT(event): void{
    this.tributos = event;
  }

  changeRMil(event): void{
    this.cantMil = event;
    if (this.cantMil == '1'){
      this.renderer.setStyle(this.containerTab.nativeElement, 'display', 'block');
    }else{
      this.renderer.setStyle(this.containerTab.nativeElement, 'display', 'none');
    }
  }

  changeAnimal(event): void{
    this.animals = event;
  }

  changeQuim(event): void{
    this.quimicProducts = event;
  }

  changeMerc(event): void{
    this.quimicProducts = event;
    if (this.quimicProducts == '1'){
      this.renderer.setStyle(this.table2.nativeElement, 'display', 'block');
    }else{
      this.renderer.setStyle(this.table2.nativeElement, 'display', 'none');
    }
  }

  addTab(): void{
    if (this.origen !== '' && this.destino !== '' && this.valor !== null && this.titulo !== ''){
      this.tabMoney = {
        tipoMoneda: this.moneySelected,
        origen: this.origen,
        destino: this.destino,
        valor: this.valor,
        titulo: this.titulo
      };
      this.tabMoneys.push(this.tabMoney);
      console.log(this.tabMoneys);
    }else{
      alert('No se puede ingresar campos vacios');
    }
  }

  capturar(): void{
    this.moneySelected = this.money;
    console.log(this.money);
  }

  delete( num: number , tab: string): void{
    if(tab === '1'){
      this.tabMoneys.splice(num, 1);
    }else{
      this.listMercancias.splice(num, 1);
    }
  }

  agregarMercancias(): void{
    if (this.cantidad !== null && this.descripcion !== '' && this.valorDolar !== null){
      this.mercancia = {
        cantidad: this.cantidad,
        descipcion: this.descripcion,
        valor: this.valorDolar
      };
      this.listMercancias.push(this.mercancia);
      console.log(this.listMercancias);
    }else{
      alert('Los datos no pueden estar vacios');
    }
  }
}
